@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background: url(/NewsWave/static/media/white.914cb53a.png) center center/cover;
}


a:link,
a:hover,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}
.footer {
    background-color: rgb(75, 70, 70);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    color: white;
  }
  .name {
    font-size: 17px;
    padding: 20px 30px 15px;
    color: white;
  }
  
  a {
    color: white;
  }
  
  .iconContainer {
    width: 30%;
    height: 50px;
    padding: 10px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;

 
}

.navbar > img {
    width: 295px;
    height: 60px;
    cursor: pointer;
    padding-top: 13px;
    padding-left: 80px ; 
} 

.menu {
    padding-top: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-flex:0.99 1;
            flex:0.99 1;
}

.navbar {
 
    
    height: 71px;
    width:100%;
    box-shadow:  0px 1px 8px #000000;
    border-bottom: 10px ;
    display: -webkit-flex;
    display: flex;
    background-color:rgb(0, 0, 0);
   position: fixed;
  
  
    
}

 
.menu-icon {
    width: 70px;
    height: 35px;
    color: white;
}


.uppercase{
    text-transform: capitalize;
}
.newsDisplay{
 
    display: -webkit-flex;
 
    display: flex;
    width: 100%;
    background-color: white;
    padding: 10px;
    margin-bottom: 25px;
    border-radius: 5px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.newsText{
    padding: 0 0 0 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.newsImage {
    object-fit: cover;
    width: 320px;
    height: 268px;
  }

  .author {
    font-size: 12px;
    font-weight: 300;
    line-height: 22px;
    color: #808290;
  }
  
  .lowerNewsText {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 10px 0;
  }
  
  .readmore {
    font-size: 12px;
    font-weight: 400;
    padding-top: 10px;
  }
  
  .source {
    text-decoration: none;
    color: black;
  }

  .title {
    font-size: 22px;
    line-height: 27px;
    font-family: "Roboto", sans-serif;
  }
  
  .faded {
    font-weight: 100;
  }
  
  .description {
    font-size: 16px;
    line-height: 22px;
    color: #44444d;
  }
  
  @media (max-width: 800px) {
    .newsDisplay {
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100%;
    }
    .newsImage {
      width: 100%;
      height: 100%;
      padding-bottom: 20px;
    }
  }
  
.content {
   
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

.containerM{
  padding-top: 90px;
   
}
  
  .loadMore {
    cursor: pointer;
    background-color: white;
    border: none;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    padding: 7px 25px;
    font-size: 13px;
    margin-top: -20px;
  }
